import React, { useState } from 'react';
import ReactMapGL, { Marker, NavigationControl, GeolocateControl } from 'react-map-gl';
import originMarker from '../graphics/marker1.svg';
import destinationMarker from '../graphics/marker2.svg';

const Map = ({origin, setOrigin, destinations, setDestinations}) => {
    const [viewPort, setViewPort] = useState({
        latitude: origin[1],
        longitude: origin[0], 
        width: "75vw",
        height: "100vh",
        zoom: 14
    });

    return ( 
        <div id="map">
            <ReactMapGL
            {...viewPort}
            mapboxApiAccessToken={"pk.eyJ1IjoiZmVybWl2ZXJzZSIsImEiOiJja2Q2YzJ3Zjkwam1mMnFuMG1zNjg5eDhmIn0.SwCaqFKXTC_-WkcbYqFcsQ"}
            mapStyle={"mapbox://styles/fermiverse/ckd7yqq7u084t1iqj97nvbjc6"}
            onViewportChange={(viewPort) => {
                setViewPort(viewPort)
            }}>
                <div style={{position: 'absolute', right: "15px", top: "30px"}}>
                    <NavigationControl />
                </div>
                <div style={{position: "absolute", top: "150px", right: "15px"}}>
                    <GeolocateControl 
                    onViewportChange={(view) => {
                        setViewPort({
                            latitude: origin[1] || 77.516233,
                            longitude: origin[0] || 28.494164,
                            width: "75vw",
                            height: "100%",
                            zoom: 12
                        })
                    }}
                    />
                </div>
                {origin.length ? (
                    <Marker
                    latitude={origin[1]}
                    longitude={origin[0]}>
                        <img src={originMarker} alt="marker" title="origin" width="32px"></img>
                    </Marker>
                ) : (null)}
                {destinations.map(destination => (
                    <Marker
                    key={String(destination[1])}
                    latitude={destination[1]}
                    longitude={destination[0]}>
                        <img src={destinationMarker} alt="marker" title="destination" width="32px"></img>
                    </Marker>
                ))}
                
                <button className="rounded" id="reset" onClick={() => {
                    setOrigin([]);
                    setDestinations([]);
                }}>Reset</button>
            </ReactMapGL>
        </div>
     );
}

export default Map;

