import React, { useState } from 'react';
import Map from './components/Map';
import Sidebar from './components/Sidebar';


const App = () => {
  const [origin, setOrigin] = useState([77.516233, 28.494164]);
  const [destinations, setDestinations] = useState([]);

  return (
    <div className="App">
      <Sidebar destinations={destinations} setDestinations={setDestinations} origin={origin} setOrigin={setOrigin} />
      <Map origin={origin} setOrigin={setOrigin} destinations={destinations} setDestinations={setDestinations}/>
    </div>
  );
}

export default App;
